.Profile-container {
    padding: 30px;
    min-height: 90vh;
    background-color: hsl(252, 30%, 95%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.EditProfile-container {
    padding: 30px;
}

.Profile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.Profile-heading {
    margin: 0;
    padding: 0;
}

@media(max-width: 350px) {
    .Profile-header {
        flex-direction: column;
        gap: 1rem;
    }

    .Profile-heading {
        text-align: center;
    }
}

.Profile-card {
    background-color: #fff;
    color: rgba(52, 49, 76, 1);
    -webkit-transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 4px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
    width: 100%;
    padding: 20px;
}

.buttonProgress {
    position: absolute;
    width: 24px;
    height: 24px;
    top: 6px;
    left: 18px;
}

.profile-box {
    background: rgba(34,42,69, 0.96);
    width:100%;
    max-width: 600px;
    margin: auto;
    text-align: center;
    padding: 25px 90px;
    color: #fff;
    position: relative;
    border-radius: 20px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
}

.profile-pic {
    width: 100%;
    max-width: 100px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background: #fff;
    padding: 6px;
    margin-top: 0;
    object-fit: cover;
    object-position: center;
}

.profile-box h3 {
    font-size: 18px;
    margin-top: 5px;
    font-weight: 500;
    margin-bottom: 0px;
}

.profile-box .role {
    margin: 0;
    padding: 0;
    font-size: 14px;
    margin-bottom: 20px;
}


.profile-bottom {
    background: #fff;
    color: #999;
    padding: 40px 30px;
    margin-right: -90px;
    margin-left: -90px;
    margin-bottom: -40px;
    border-radius: 20px;
    margin-top: -10px;
}

.profile-box > button {
    background: #fff;
    color: rgb(25, 118, 210);
    border: none;
    outline: none;
    box-shadow: 0 5px 10px rgb(25, 118, 210, 0.5);
    padding: 10px 30px;
    cursor: pointer;
    border-radius: 30px;
    margin-bottom: -50px;
    font-weight: 600;
    font-size: 15px;
}

.btn-container {
    display: flex;
    gap: 1rem;
    margin-top: 30px;
    margin-bottom: 0;
    padding-bottom: 0;
}

.profile-bottom > .btn-password {
    margin-top: 20px;
    background: #fff;
    color: rgb(25, 118, 210);
    border: none;
    outline: none;
    box-shadow: 0 5px 10px rgb(25, 118, 210, 0.5);
    padding: 10px 30px;
    cursor: pointer;
    border-radius: 30px;
    font-weight: 600;
    font-size: 15px;
}

.profile-field {
    display: flex;
    flex-direction: column;
    text-align: start;
}

.profile-field label {
    color: #333;
    font-weight: 500;
}

.profile-field span {
    font-size: 13px;
    color: #555;
}

.phone-field {
    display: flex;
    flex-direction: column;
    text-align: start;
}

.phone-field label {
    color: #333;
    font-weight: 500;
}

.phone-field span {
    font-size: 13px;
    color: #555;
}

.phone_line {
    width: 50%;
    height: 2px;
    margin: 0 auto;
    background-color: #333;
    margin-top: 35px;
}

.phone_line_bar {
    width: 10%;
    height: 2px;
    margin: 0 auto;
    background-color: #333;
    margin-top: 20px;
}

@media(max-width: 350px) {
    .profile-box{
        padding: 20px 90px;
    }

    .profile-box h3 {
        font-size: 16px;
    }

    .profile-box .role {
        font-size: 12px;
    }

    .profile-box > button{
        text-align: center;
        width: 80%;
        font-size: 14px;
        margin-bottom: -5px;
        position: relative;
    }

    .profile-box > button > span {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .profile-bottom > .btn-password {
        font-size: 12px;
    }
}